import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import {Link, withRouter} from 'react-router-dom';
import Icon from "@material-ui/core/Icon";
import {useSelector} from "react-redux";

const NavBar = (props) => {
    const state = useSelector(state => state);

    const handleDisable = (action) => {
        switch(action) {
            case 'calibration':
                return !(state.availableSensors.gyroscope || state.availableSensors.gyroscope);

            case 'evaluation':
                return (state.calibration.measurement1 === null || state.calibration.measurement2 === null || state.calibration.inertia === "");

            default:
                return false;
        }
    };

    return (
        <AppBar position={"fixed"}>
            <Toolbar>
                <Icon style={{paddingRight: 12}}>
                    3d_rotation
                </Icon>
                <Typography variant={"h6"}>
                    Trägheitsmoment App
                </Typography>
            </Toolbar>
            <Tabs variant="scrollable" value={props.location.pathname} scrollButtons={'on'}>
                <Tab
                    component={Link}
                    label={"Start"}
                    to={'/'}
                    value={'/'}
                />
                <Tab
                    component={Link}
                    label={"Einstellung"}
                    to={'/settings'}
                    value={'/settings'}
                />
                <Tab
                    component={Link}
                    label={"Kalibrierung"}
                    to={'/calibration'}
                    value={'/calibration'}
                    disabled={handleDisable('calibration')}
                />
                <Tab
                    component={Link}
                    label={"Bestimmung"}
                    to={'/evaluation'}
                    value={'/evaluation'}
                    disabled={handleDisable('evaluation')}
                />
            </Tabs>
        </AppBar>
    )
};

export default withRouter(NavBar);
