import React, {useEffect} from 'react';
import {makeStyles} from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import {useDispatch, useSelector} from "react-redux";

const useStyles = makeStyles(theme => ({
    root: {
        position: "fixed",
        height: "100%",
        width: "100%",
        top: "50%",
        left: "50%",
        backgroundColor: "rgba(0, 0, 0, 0.2)",
        zIndex: 1300,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        overflow: "hidden",
        transform: "translateX(-50%) translateY(-50%) scaleX(0) scaleY(0)",
        transition: "all 0.2s ease",
        opacity: 0,
    },
    active: {
        transform: "translateX(-50%) translateY(-50%) scaleX(1) scaleY(1)",
        opacity: 1
    }
}));

const Loader = (props) => {
    const classes = useStyles();

    const measuring = useSelector(state => state.measuring);
    const duration = useSelector(state => state.settings.duration + state.settings.delay);

    const dispatch = useDispatch();

    useEffect(() => {
        window.setTimeout(function() {
            if(measuring) {
                dispatch({type: "MEASURING"});
            }
        }, duration * 1000);

        return (window.clearTimeout(function() {
            if(measuring) {
                dispatch({type: "MEASURING"});
            }
        }));
    });

    return (
        <div className={classes.root + " " + (measuring ? classes.active : "")}>
            <CircularProgress color={"secondary"} size={100} thickness={2} />
        </div>
    )
};

export default Loader;
