import React from 'react';
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import {useDispatch, useSelector} from "react-redux";
import {Measure, Round, Inertia} from "../Functions/Measure";

const Evaluation = (props) => {
    const dispatch = useDispatch();

    const measurements = useSelector(state => state.evaluation.measurements);
    const settings = useSelector(state => state.settings);
    const calibration = useSelector(state => state.calibration);

    const handleMeasurement = () => {
        Measure(settings.duration, settings.delay, settings.sensorType, storeMeasurement);
        dispatch({type: "MEASURING"});
    };

    const storeMeasurement = (measurement) => {
        dispatch({type: "EVALUATION", payload: {measurement: measurement}});
        dispatch({type: "MEASURING"});
    };

    return (
        <div>
            <Typography variant={"body1"} gutterBottom align={"justify"}>
                Hier können Sie das Trägheitsmoment nach erfolgter Messung berechnen. Dafür müssen Sie nur noch das Bekannte Trägheitsmoment eintragen.
            </Typography>
            <br />
            <div style={{justifyContent: "center", display: "flex"}}>
                <Button color={"primary"} variant={"contained"} onClick={() => handleMeasurement()}>
                    Trägheitsmoment messen
                </Button>
            </div>
            <br />
            <Table size={"small"}>
                <TableHead>
                    <TableRow>
                        <TableCell>Messung</TableCell>
                        <TableCell>T in s</TableCell>
                        <TableCell>f in Hz</TableCell>
                        <TableCell>J in Nm</TableCell>
                    </TableRow>
                    {measurements.map((x, index) => (
                        <TableRow
                            key={index}
                        >
                            <TableCell>{index}</TableCell>
                            <TableCell>{Round(x.T, 4)}</TableCell>
                            <TableCell>{Round(1 / x.T, 4)}</TableCell>
                            <TableCell>{Inertia(parseFloat(calibration.inertia), calibration.measurement1.T, calibration.measurement2.T, x.T).toExponential(4)}</TableCell>
                        </TableRow>
                        )
                    )}
                </TableHead>
            </Table>
        </div>
    )
};

export default Evaluation;
