const initialState = {
    measuring: false,
    availableSensors:{
        gyroscope: true,
        accelerometer: true,
    },
    settings: {
        sensorType: null,
        duration: 30000,
        delay: 2000,
    },
    calibration: {
        inertia: "",
        measurement1: null,
        measurement2: null,
    },
    evaluation: {
        measurements: [],
    }
};

export default function appReducer(state = initialState, action) {
    switch(action.type) {
        case "AVAILABLE_SENSORS":
            return {
                ...state,
                availableSensors: {
                    gyroscope: action.payload.gyroscope,
                    accelerometer: action.payload.accelerometer,
                }
            };
        case "DURATION":
            return {
                ...state,
                settings: {
                    ...state.settings,
                    duration: parseInt(action.payload.duration),
                }
            };
        case "DELAY":
            return {
                ...state,
                settings: {
                    ...state.settings,
                    delay: parseInt(action.payload.delay),
                }
            };
        case "SENSOR_TYPE":
            return {
                ...state,
                settings: {
                    ...state.settings,
                    sensorType: action.payload.sensorType,
                }
            };
        case "CALIBRATION1":
            return {
                ...state,
                calibration: {
                    ...state.calibration,
                    measurement1: action.payload.measurement
                }
            };
        case "CALIBRATION2":
            return {
                ...state,
                calibration: {
                    ...state.calibration,
                    measurement2: action.payload.measurement
                }
            };
        case "INERTIA":
            return {
                ...state,
                calibration: {
                    ...state.calibration,
                    inertia: action.payload.inertia
                }
            };
        case "EVALUATION":
            return {
                ...state,
                evaluation: {
                    measurements: [...state.evaluation.measurements, action.payload.measurement]
                }
            };
        case "MEASURING":
            return {
                ...state,
                measuring: !state.measuring
            };
        default:
            return state;
    }
}
