const Measure = (duration, delay, sensor, handle) => {

    const finished = () => {
        analyseData();
        handle(measurement);
        window.removeEventListener('devicemotion', handleMeasurement, false);
    };

    const measurement = {
        x: [],
        y: [],
        z: [],
        timeStamp: [],
        T: null,
    };

    const analyseData = () => {
        let data = measurement.z;
        let time = measurement.timeStamp;

        if(sensor === 'accelerometer') {
            let temp = [];
            for(let i = 0; i < measurement.x.length; i++) {
                temp.push(measurement.x[i] + measurement.y[i]);
            }
            data = temp;
        }

        let zero = [];
        let direction = Math.sign(data[0]);

        for(let i = 1; i < data.length; i++) {
            if(direction !== Math.sign(data[i])) {
                zero.push(time[i] / 1000);
                direction *= -1;
            }
        }

        let deltaT = 0;

        for(let i = 0; i < zero.length - 1; i++) {
            deltaT += zero[i + 1] - zero[i];
        }

        deltaT = deltaT / (zero.length - 1) * 2;

        measurement.T = deltaT;
    };

    const handleMeasurement = (event) => {
        if(sensor === 'accelerometer') {
            measurement.x.push(event.acceleration.x);
            measurement.y.push(event.acceleration.y);
            measurement.z.push(event.acceleration.z);
        }

        if(sensor === 'gyroscope') {
            measurement.x.push(event.rotationRate.alpha);
            measurement.y.push(event.rotationRate.beta);
            measurement.z.push(event.rotationRate.gamma);
        }

        measurement.timeStamp.push(event.timeStamp);
    };

    const startMeasurement = () => {
        window.addEventListener('devicemotion', handleMeasurement);
        window.setTimeout(finished, duration);
        window.clearTimeout(startMeasurement);
    };

    window.setTimeout(startMeasurement, delay);
};

const Round = (value, decimal) => {
    return Math.round(value * Math.pow(10, decimal)) / Math.pow(10, decimal);
};

const Inertia = (j1, t0, t1, t2) => {
    j1 = parseFloat(j1);
    return (j1 * (t2 * t2 - t0 * t0) / (t1 * t1 - t0 * t0));
};

export {Measure, Round, Inertia};
