import React from 'react';
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TextField from "@material-ui/core/TextField";
import {useDispatch, useSelector} from "react-redux";
import {Measure, Round} from "../Functions/Measure";

const Calibration = (props) => {
    const dispatch = useDispatch();

    const settings = useSelector(state => state.settings);
    const calibration = useSelector(state => state.calibration);

    const handleInertia = (event) => {
        dispatch({type: "INERTIA", payload: {inertia: event.target.value}});
    };

    const handleCalibration = (index) => {
        if(index === 1) Measure(settings.duration, settings.delay, settings.sensorType, sendCalibration1ToStore);
        if(index === 2) Measure(settings.duration, settings.delay, settings.sensorType, sendCalibration2ToStore);
        dispatch({type: "MEASURING"});
    };

    const sendCalibration1ToStore = (measurement) => {
        dispatch({type: "CALIBRATION1" , payload: {measurement: measurement}});
        dispatch({type: "MEASURING"});
    };

    const sendCalibration2ToStore = (measurement) => {
        dispatch({type: "CALIBRATION2" , payload: {measurement: measurement}});
        dispatch({type: "MEASURING"});
    };

    const renderMeasurements = () => {
        const data = [calibration.measurement1, calibration.measurement2];

        return (
            data.map((x, i) => (
                x === null ? <TableRow key={i}/> :
                    (
                        <TableRow key={i}>
                            <TableCell>{i}</TableCell>
                            <TableCell>{Round(x.T, 4)}</TableCell>
                            <TableCell>{Round(1/x.T, 4)}</TableCell>
                        </TableRow>)
                    ))
        )
    }

    return (
        <div>
            <Typography variant={"body1"} component={"div"} align={"justify"} gutterBottom>
                Führen Sie nacheinander die 2 Kalibrierungsmessungen durch, beachten Sie dabei die erwähnten Richtlinien. Geben Sie bitte auch das bekannte Trägheitsmoment in das Textfeld ein.
            </Typography>
            <br />
            <div style={{justifyContent: "center", display: "flex"}}>
                <ButtonGroup color={"primary"} variant={"contained"}>
                    <Button onClick={() => handleCalibration(1)}>
                        Messung 1
                    </Button>
                    <Button onClick={() => handleCalibration(2)} disabled={calibration.measurement1 === null}>
                        Messung 2
                    </Button>
                </ButtonGroup>
            </div>
            <br />
            <TextField
                label={"Bekanntes Trägheitsmoment in Nm"}
                required
                component={"div"}
                multiline
                fullWidth
                rows={1}
                onChange={(event) => handleInertia(event)}
                value={calibration.inertia}
            />
            <br />
            <br />
            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell>Messung</TableCell>
                        <TableCell>T in s</TableCell>
                        <TableCell>f in Hz</TableCell>
                    </TableRow>
                    {renderMeasurements()}
                </TableHead>
            </Table>
        </div>
    )
};

export default Calibration;
