import React from 'react';
import Modal from "@material-ui/core/Modal";
import Typography from "@material-ui/core/Typography";
import {useDispatch, useSelector} from "react-redux";
import {CheckSupport} from "../Functions/CheckSupport";
import Icon from "@material-ui/core/Icon";

const Unsupported = (props) => {
    const supported = useSelector(state => state.availableSensors.gyroscope || state.availableSensors.accelerometer);

    const dispatch = useDispatch();

    const setSupportLevel = (support) => {
        dispatch({type: "AVAILABLE_SENSORS", payload: support});
        if(support.gyroscope) {
            dispatch({type: "SENSOR_TYPE", payload: {sensorType: 'gyroscope'}});
            return;
        }
        if(support.accelerometer) {
            dispatch({type: "SENSOR_TYPE", payload: {sensorType: 'accelerometer'}});
        }
    };

    CheckSupport(setSupportLevel);

    return (
        <Modal open={!supported} style={{display: "flex", justifyContent: "center", alignItems: "center"}}>
            <div style={{backgroundColor: "white", padding: 30, margin: 20, borderRadius: 5, display: "flex", alignItems: "center"}}>
                <Icon style={{paddingRight: 20, color: "red"}}>error</Icon>
                <Typography>Ihr Gerät / Browser wird leider nicht unterstützt!</Typography>
            </div>
        </Modal>
    );
};

export default Unsupported;
