import React from 'react';
import Typography from "@material-ui/core/Typography";
import MathJax from 'react-mathjax';
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";

const Landing = (props) => {
    return (
        <MathJax.Provider>
            <Typography variant={"body1"} component={"div"} align={"justify"}>
                Bitte lesen Sie sich alle Reiter durch und arbeiten diese ab. Um die Bestimmung des Trägheitsmomentes
                durchführen zu können müssen Sie zuerst die Kalibrierung durchführen.
            </Typography>
            <br />
            <Divider />
            <br />
            <Typography variant={"body1"} component={"div"} align={"justify"}>
                Die dieser App zugrundliegende Formel für die Bestimmung des Trägheitsmomentes lautet:
                <MathJax.Node formula={"J_x = J_1 \\cdot \\frac{T_x^2-T_0^2}{T_1^2-T_0^2}"} />
            </Typography>
            <Typography variant={"body1"} component={"div"} align={"justify"}>
                Um das Trägheitsmoment <MathJax.Node inline formula={"J_x"} /> zu bestimmen müssen 3 Messungen durchgeführt werden:
                <List>
                    <ListItem>
                        <ListItemText>
                            <MathJax.Node inline formula={"T_0"} />: wird nur mit dem Smartphone auf dem Drehteller durchgeführt.
                        </ListItemText>
                    </ListItem>
                    <ListItem>
                        <ListItemText>
                            <MathJax.Node inline formula={"T_1"} />: wiederum mit einem Objekt dessen Trägheitsmoment um die gewünschte Achse bekannt ist.
                        </ListItemText>
                    </ListItem>
                    <ListItem>
                        <ListItemText>
                            <MathJax.Node inline formula={"T_x"} />: wird mit einem beliebigen Körper dessen Trägheitsmoment bestimmt werden soll durchgeführt.
                        </ListItemText>
                    </ListItem>
                </List>
            </Typography>
        </MathJax.Provider>
    )
};

export default Landing;
