import React from 'react';
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import Radio from "@material-ui/core/Radio";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import Slider from '@material-ui/lab/Slider'
import {useDispatch, useSelector} from "react-redux";

const Settings = (props) => {
    const dispatch = useDispatch();

    const availableSensors = useSelector(state => state.availableSensors);
    const settings = useSelector(state => state.settings);

    const handleDurationChange = (value) => {
        dispatch({type: "DURATION", payload: {duration: parseInt(value) * 1000}});
    };

    const handleDelayChange = (value) => {
        dispatch({type: "DELAY", payload: {delay: parseInt(value) * 1000}});
    };

    const handleSensorTypeChange = (event, value) => {
        dispatch({type: "SENSOR_TYPE", payload: {sensorType: value}});
    };

    return (
        <div>
            <Typography variant={"body1"} component={"p"} align={"justify"}>
                Wählen Sie hier bitte ihren gewünschten Sensor aus, sollte einer ausgegraut sein so kann nur der andere Sensor benutzt werden.
            </Typography>
            <br />
            <FormControl>
                <FormLabel>Sensor</FormLabel>
                <RadioGroup onChange={(event, value) => handleSensorTypeChange(event, value)} value={settings.sensorType}>
                    <FormControlLabel value={"gyroscope"} disabled={!availableSensors.gyroscope} control={<Radio />} label={"Gyroskop"} />
                    <FormControlLabel value={"accelerometer"} disabled={!availableSensors.accelerometer} control={<Radio />} label={"Beschleunigungssensor"} />
                </RadioGroup>
            </FormControl>
            <Divider style={{margin: "18px 0 18px 0"}}/>
            <Typography variant={"body1"} component={"p"} align={"justify"}>
                Hier können Sie die Messdauer und Messverzögerung einstellen.
            </Typography>
            <br />
            <Typography variant={"body1"} component={"p"} color={"textSecondary"} align={"justify"} gutterBottom>
                Messdauer in Sekunden
            </Typography>
            <Slider
                value={settings.duration / 1000}
                step={5}
                max={120}
                min={10}
                valueLabelDisplay={"auto"}
                onChange={(event, value) => handleDurationChange(value)}
            />
            <Typography variant={"body1"} component={"p"} color={"textSecondary"} align={"justify"} gutterBottom>
                Messverzögerung in Sekunden
            </Typography>
            <Slider
                value={settings.delay / 1000}
                step={1}
                max={10}
                min={0}
                valueLabelDisplay={"auto"}
                onChange={(event, value) => handleDelayChange(value)}
            />
        </div>
    )
};

export default Settings;
